.page-tribes__one {
  background: #000;
  min-height: 100vh;
  color: $primary;
  padding: 10.417vw 5vw 9.375vw;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  .tagline-marquee {
    display: block;
    width: 100%;
    letter-spacing: 1.2vw; }
  h1 {
    font-size: 22.865vw;
    line-height: 1em;
    @include font-druk();
    // font-weight: 400
    color: $primary; }
  .desc {
    width: 100%;
    margin: 0 auto;
    text-align: center;
    padding-left: 10vw;
    padding-right: 10vw;
    p {
      color: #fff;
      font-size: 30px;
      line-height: 36px; } }
  .wrap {
    position: relative;
    width: 100%; }
  .arrow {
    position: absolute;
    bottom: 3.646vw;
    left: 0;
    right: 0;
    margin: 0 auto;
    img {
      display: block;
      margin: 0 auto;
      width: 24px;
      height: 40px; } }
  @include maxtablet() {
    min-height: 80vh;
    h1 {
      font-size: 40vw;
      line-height: 1em; }
    .desc {
      p {
        font-size: 16px;
        line-height: 1.2em; } } } }

.page-tribes__two {
  background: $primary;
  .item {
    display: block;
    position: relative;
    width: 100%;
    max-width: 25%;
    flex: 0 0 25%;
    min-height: 25vw;
    &__logo {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
      padding: 1vw;
      img {
        display: block;
        max-width: 80%;
        max-height: 80%;
        width: auto;
        height: auto; } }
    &__title {
      display: block;
      font-size: 20px;
      line-height: 1.2em;
      text-align: center;
      color: #000; }
    &__wrap {
      padding: 1vw;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      border-right: 1px solid #000;
      border-bottom: 1px solid #000; } }
  .wrap {
    display: flex;
    flex-wrap: wrap; }

  @include maxtablet() {
    .item {
      max-width: 50%;
      flex: 0 0 50%;
      min-height: 50vw;
      &__logo {
        padding: 4vw;
        img {
          max-width: 60%;
          max-height: 60%; } }
      &:nth-child(2n) {
        .item__wrap {
          border-right: 0; } } } } }

.page-tribe-s__one {
  background: #000;
  color: $primary;
  padding: 10.417vw 64px 60px;
  position: relative;
  h1 {
    display: block;
    width: 100%;
    max-width: 80%;
    flex: 0 0 80%;
    font-size: 10vw;
    line-height: 1em;
    @include font-druk();
    color: $primary;
    font-weight: 500;
    margin-bottom: 0; }
  .desc {
    display: block;
    width: 100%;
    max-width: 20%;
    flex: 0 0 20%;
    margin-bottom: 0;
    p {
      color: #FFF;
      font-size: 18px;
      line-height: 1.3em;
      margin-bottom: 40px; } }
  .img {
    display: block;
    width: 100%;
    padding-top: 60px;
    img {
      display: block;
      width: 100%;
      height: auto; }
    .caption {
      padding-top: 30px;
      display: block;
      width: 100%;
      font-size: 30px;
      line-height: 1.3em;
      font-weight: 600;
      color: #FFF; } }
  .wrap {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    align-items: center; }
  @include maxtablet() {
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 60px;
    padding-bottom: 60px;
    h1 {
      max-width: 100%;
      flex: 0 0 100%;
      font-size: 20vw;
      margin-bottom: 40px; }
    .desc {
      max-width: 100%;
      flex: 0 0 100%;
      p {
        font-size: 16px;
        line-height: 1.5em;
        margin-bottom: 24px; } }
    .img {
      padding-top: 30px;
      .caption {
        font-size: 18px; } } } }

.page-tribe-s__two {
  background: #000;
  padding-bottom: 160px;
  article {
    color: #FFF;
    p {
      color: #FFF;
      font-size: 29px;
      line-height: 1.3em;
      margin-bottom: 40px; } }
  .article-top {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-left: -15px;
    margin-right: -15px;
    p {
      font-size: 19px;
      line-height: 1.5em;
      font-weight: 600;
      color: #FFF;
      &:last-child {
        margin-bottom: 0; } }
    a {
      font-size: 19px;
      line-height: 1.5em;
      font-weight: 600;
      color: #FFF;
      &:hover {
        text-decoration: underline; } }
    .tr {
      text-align: right; }
    .tc {
      text-align: center; }
    .col {
      display: block;
      width: 100%;
      max-width: 33.333333%;
      flex: 0 0 33.333333%;
      padding-left: 15px;
      padding-right: 15px;
      padding-bottom: 80px; } }
  .wrap {
    padding-left: 64px;
    padding-right: 64px; }
  @include maxtablet {
    padding-bottom: 60px;
    .article-top {
      padding-bottom: 30px;
      .col {
        max-width: 100%;
        flex: 0 0 100%;
        padding-bottom: 30px;
        text-align: left !important; }
      p {
        font-size: 14px;
        line-height: 1.5em;
        font-weight: 400; }
      a {
        font-size: 14px;
        line-height: 1.5em;
        font-weight: 400; } }
    article {
      p {
        font-size: 20px;
        line-height: 1.5em; } }
    .wrap {
      padding-left: 20px;
      padding-right: 20px; } } }

.page-tribe-s__three {
  .wrap {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    .img-full {
      display: block;
      width: 100%;
      max-width: 100%;
      flex: 0 0 100%; }
    .img-half {
      display: block;
      width: 100%;
      max-width: 50%;
      flex: 0 0 50%; } } }


