.page-project__one {
  background: #000;
  min-height: 100vh;
  color: $primary;
  padding: 10.417vw 5vw 9.375vw;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  .tagline-marquee {
    display: block;
    width: 100%;
    letter-spacing: 1.2vw; }
  h1 {
    font-size: 22.865vw;
    line-height: 1em;
    @include font-druk();
    // font-weight: 400
    color: $primary; }
  .desc {
    width: 100%;
    margin: 0 auto;
    text-align: center;
    padding-left: 10vw;
    padding-right: 10vw;
    p {
      color: #fff;
      font-size: 30px;
      line-height: 36px; } }
  .wrap {
    position: relative;
    width: 100%; }
  .arrow {
    position: absolute;
    bottom: 3.646vw;
    left: 0;
    right: 0;
    margin: 0 auto;
    img {
      display: block;
      margin: 0 auto;
      width: 24px;
      height: 40px; } }
  @include maxtablet() {
    min-height: 80vh;
    h1 {
      font-size: 40vw;
      line-height: 1em; }
    .desc {
      p {
        font-size: 16px;
        line-height: 1.2em; } } } }

.page-project__two {
  background: #000;
  .item {
    display: block;
    width: 100%;
    &__title {
      @include font-druk();
      font-size: 2.5vw;
      font-weight: 400;
      line-height: 1.2em;
      color: #FFF;
      width: 100%;
      max-width: 33.333%;
      flex: 0 0 33.333%;
      transition: all .3s ease; }
    &__meta {
      color: #FFF;
      font-size: 1.563vw;
      line-height: 1.3em;
      font-weight: 600;
      text-align: center;
      width: 100%;
      max-width: 33.333%;
      flex: 0 0 33.333%;
      transition: all .3s ease; }
    &__date {
      color: #FFF;
      font-size: 1.563vw;
      line-height: 1.3em;
      font-weight: 600;
      text-align: right;
      width: 100%;
      max-width: 33.333%;
      flex: 0 0 33.333%;
      transition: all .3s ease; }
    &__wrap {
      display: flex;
      width: 100%;
      align-items: center;
      justify-content: space-between;
      padding-left: 62px;
      padding-right: 62px;
      border-top: 2px solid $primary; }
    &:last-child .item__wrap {
      border-bottom: 2px solid $primary; }
    &:hover {
      .item__title,
      .item__meta,
      .item__date {
        color: $primary; } } }

  .wrap {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat; }

  @include maxtablet() {
    .item {
      &__wrap {
        padding: 10px 20px; }
      &__title {
        font-size: 16px; }
      &__meta {
        font-size: 11px; }
      &__date {
        font-size: 12px; } } } }


.page-project-s__one {
  background: #000;
  color: $primary;
  padding: 10.417vw 64px 130px;
  position: relative;
  h1 {
    display: block;
    width: 100%;
    max-width: 80%;
    flex: 0 0 80%;
    font-size: 10vw;
    line-height: 1em;
    @include font-druk();
    color: $primary;
    font-weight: 500;
    margin-bottom: 0; }
  .desc {
    display: block;
    width: 100%;
    max-width: 20%;
    flex: 0 0 20%;
    margin-bottom: 0;
    p {
      color: #FFF;
      font-size: 18px;
      line-height: 1.3em;
      margin-bottom: 40px; } }
  .img {
    display: block;
    width: 100%;
    padding-top: 60px;
    img {
      display: block;
      width: 100%;
      height: auto; }
    .caption {
      padding-top: 30px;
      display: block;
      width: 100%;
      font-size: 30px;
      line-height: 1.3em;
      font-weight: 600;
      color: #FFF; } }
  .wrap {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    align-items: center; }
  @include maxtablet() {
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 60px;
    padding-bottom: 60px;
    h1 {
      max-width: 100%;
      flex: 0 0 100%;
      font-size: 20vw;
      margin-bottom: 40px; }
    .desc {
      max-width: 100%;
      flex: 0 0 100%;
      p {
        font-size: 16px;
        line-height: 1.5em;
        margin-bottom: 24px; } }
    .img {
      padding-top: 30px;
      .caption {
        font-size: 18px; } } } }

.page-project-s__two {
  background: #000;
  padding-bottom: 160px;
  article {
    color: #FFF;
    p {
      color: #FFF;
      font-size: 29px;
      line-height: 1.3em;
      margin-bottom: 40px; } }
  .article-top {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-left: -15px;
    margin-right: -15px;
    p {
      font-size: 19px;
      line-height: 1.5em;
      font-weight: 600;
      color: #FFF;
      &:last-child {
        margin-bottom: 0; } }
    a {
      font-size: 19px;
      line-height: 1.5em;
      font-weight: 600;
      color: #FFF;
      &:hover {
        text-decoration: underline; } }
    .tr {
      text-align: right; }
    .tc {
      text-align: center; }
    .col {
      display: block;
      width: 100%;
      max-width: 33.333333%;
      flex: 0 0 33.333333%;
      padding-left: 15px;
      padding-right: 15px;
      padding-bottom: 80px; } }
  .wrap {
    padding-left: 64px;
    padding-right: 64px; }
  @include maxtablet {
    padding-bottom: 60px;
    .article-top {
      padding-bottom: 30px;
      .col {
        max-width: 100%;
        flex: 0 0 100%;
        padding-bottom: 30px;
        text-align: left !important; }
      p {
        font-size: 14px;
        line-height: 1.5em;
        font-weight: 400; }
      a {
        font-size: 14px;
        line-height: 1.5em;
        font-weight: 400; } }
    article {
      p {
        font-size: 20px;
        line-height: 1.5em; } }
    .wrap {
      padding-left: 20px;
      padding-right: 20px; } } }

.page-project-s__three {
  .wrap {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    .img-full {
      display: block;
      width: 100%;
      max-width: 100%;
      flex: 0 0 100%; }
    .img-half {
      display: block;
      width: 100%;
      max-width: 50%;
      flex: 0 0 50%; } } }



