

.page-flagship__one {
  background: #000;
  min-height: 100vh;
  color: $primary;
  padding: 10.417vw 5vw 9.375vw;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  .tagline-marquee {
    display: block;
    width: 100%;
    letter-spacing: 1.2vw; }
  h1 {
    font-size: 22.865vw;
    line-height: 1em;
    @include font-druk();
    // font-weight: 400
    color: $primary; }
  .desc {
    width: 100%;
    margin: 0 auto;
    text-align: center;
    padding-left: 10vw;
    padding-right: 10vw;
    p {
      color: #fff;
      font-size: 30px;
      line-height: 36px; } }
  .wrap {
    position: relative;
    width: 100%; }
  .arrow {
    position: absolute;
    bottom: 3.646vw;
    left: 0;
    right: 0;
    margin: 0 auto;
    img {
      display: block;
      margin: 0 auto;
      width: 24px;
      height: 40px; } }
  @include maxtablet() {
    h1 {
      font-size: 40vw;
      line-height: 1em; }
    .desc {
      p {
        font-size: 16px;
        line-height: 1.2em; } } } }

.page-flagship__two {
  .wrap {
    display: flex;
    flex-wrap: wrap; }
  .item {
    position: relative;
    display: block;
    width: 100%;
    max-width: 50%;
    flex: 0 0 50%;
    overflow: hidden;
    &__title {
      width: 100%;
      max-width: 100%;
      flex: 0 0 100%;
      @include font-druk();
      font-size: 5vw;
      line-height: 1em;
      font-weight: 400;
      color: $primary;
      padding: 0 2vw 1vw; }
    &__cta {
      width: 100%;
      max-width: 50%;
      flex: 0 0 50%;
      padding: 0 2vw 2vw;
      a {
        @include font-druk();
        font-size: 2vw;
        line-height: 1em;
        opacity: 1;
        color: #FFF;
        text-decoration: underline;
        &:hover {
          opacity: 0.6; } } }
    &__date {
      @include font-druk();
      width: 100%;
      max-width: 50%;
      flex: 0 0 50%;
      padding: 0 2vw 2vw;
      font-size: 2vw;
      line-height: 1em;
      color: #FFF;
      text-align: right; }
    &__date-sm {
      display: none; }
    &__bg {
      z-index: 1;
      position: absolute;
      width: 100%;
      height: 100%;
      background-size: cover;
      background-position: center;
      top: 0;
      left: 0;
      transition: all .3s ease-in-out; }
    &__content {
      z-index: 2;
      position: absolute;
      width: 100%;
      left: 0;
      bottom: 0;
      display: flex;
      flex-wrap: wrap; }
    &__wrap {
      width: 100%;
      padding-top: 60%;
      background: #000; }
    &:hover {
      .item__bg {
        transform: scale(1.03, 1.03); } }
    &.item--alt {
      .item__title {
        color: #000; }
      .item__wrap {
        background: $primary; } }
    &.item--lg {
      max-width: 100%;
      flex: 0 0 100%;
      .item__wrap {
        padding-top: 30%; } }
    &.anim {
      .item__bg {
        opacity: 0;
        transition: all .3s ease-in-out;
        transition-delay: 0.2s; }
      &.animate-in {
        .item__bg {
          opacity: 1; } } } }

  @include maxtablet {
    .item {
      max-width: 100%;
      flex: 0 0 100%;
      .item__title {
        font-size: 8vw;
        line-height: 1em;
        text-align: center;
        padding-bottom: 0;
        margin-bottom: 0; }
      .item__cta {
        text-align: center;
        max-width: 100%;
        flex: 0 0 100%;
        padding-bottom: 20px;
        a {
          font-size: 4vw;
          line-height: 1em; } }
      .item__date {
        display: none; }
      .item__date-sm {
        z-index: 3;
        display: block;
        position: absolute;
        top: 20px;
        right: 16px;
        text-align: right;
        span {
          @include font-druk();
          font-size: 4vw;
          line-height: 1em;
          color: #FFF; }
        &.dark {
          span {
            color: #000; } } }
      &.item--lg {
        .item__wrap {
          padding-top: 60%; } } } } }

.page-flagship__footer {
  background-image: url(../images/bg-sfour-ourpeople.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding: 3.125vw;
  &-top {
    display: flex;
    align-items: center;
    margin-bottom: 72px;
    justify-content: space-between;
    .left {
      display: flex;
      align-items: center;
      margin-left: -32px;
      margin-right: -32px;
      @include font-druk();
      // font-size: 39px
      font-size: 2.083vw;
      line-height: 1em;
      font-weight: 500;
      color: #000;
      .item {
        padding-right: 32px;
        padding-left: 32px; }
      a {
        color: #000;
        text-decoration: none; } }
    .right {
      @include font-druk();
      font-size: 2.083vw;
      line-height: 1em;
      font-weight: 500;
      color: $primary; } } }

.page-flagship-s__one {
  background: #000;
  color: $primary;
  padding: 10.417vw 64px 130px;
  position: relative;
  h1 {
    display: block;
    width: 100%;
    max-width: 80%;
    flex: 0 0 80%;
    font-size: 10vw;
    line-height: 1em;
    @include font-druk();
    color: $primary;
    font-weight: 500;
    margin-bottom: 0; }
  .desc {
    display: block;
    width: 100%;
    max-width: 20%;
    flex: 0 0 20%;
    margin-bottom: 0;
    p {
      color: #FFF;
      font-size: 18px;
      line-height: 1.3em;
      margin-bottom: 40px; } }
  .img {
    display: block;
    width: 100%;
    padding-top: 60px;
    img {
      display: block;
      width: 100%;
      height: auto; }
    .caption {
      padding-top: 30px;
      display: block;
      width: 100%;
      font-size: 30px;
      line-height: 1.3em;
      font-weight: 600;
      color: #FFF; } }
  .wrap {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    align-items: center; }
  @include maxtablet() {
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 60px;
    padding-bottom: 60px;
    h1 {
      max-width: 100%;
      flex: 0 0 100%;
      font-size: 20vw;
      margin-bottom: 40px; }
    .desc {
      max-width: 100%;
      flex: 0 0 100%;
      p {
        font-size: 16px;
        line-height: 1.5em;
        margin-bottom: 24px; } }
    .img {
      padding-top: 30px;
      .caption {
        font-size: 18px; } } } }

.page-flagship-s__two {
  background: #000;
  padding-bottom: 160px;
  article {
    color: #FFF;
    p {
      color: #FFF;
      font-size: 29px;
      line-height: 1.3em;
      margin-bottom: 40px; } }
  .article-top {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-left: -15px;
    margin-right: -15px;
    p {
      font-size: 19px;
      line-height: 1.5em;
      font-weight: 600;
      color: #FFF;
      &:last-child {
        margin-bottom: 0; } }
    a {
      font-size: 19px;
      line-height: 1.5em;
      font-weight: 600;
      color: #FFF;
      &:hover {
        text-decoration: underline; } }
    .tr {
      text-align: right; }
    .tc {
      text-align: center; }
    .col {
      display: block;
      width: 100%;
      max-width: 33.333333%;
      flex: 0 0 33.333333%;
      padding-left: 15px;
      padding-right: 15px;
      padding-bottom: 80px; } }
  .wrap {
    padding-left: 64px;
    padding-right: 64px; }
  @include maxtablet {
    padding-bottom: 60px;
    .article-top {
      padding-bottom: 30px;
      .col {
        max-width: 100%;
        flex: 0 0 100%;
        padding-bottom: 30px;
        text-align: left !important; }
      p {
        font-size: 14px;
        line-height: 1.5em;
        font-weight: 400; }
      a {
        font-size: 14px;
        line-height: 1.5em;
        font-weight: 400; } }
    article {
      p {
        font-size: 20px;
        line-height: 1.5em; } }
    .wrap {
      padding-left: 20px;
      padding-right: 20px; } } }

.page-flagship-s__three {
  .wrap {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    .img-full {
      display: block;
      width: 100%;
      max-width: 100%;
      flex: 0 0 100%; }
    .img-half {
      display: block;
      width: 100%;
      max-width: 50%;
      flex: 0 0 50%; } } }

