/* Header */
.header {
  z-index: 100;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 48px 62px;
  transition: all .3s ease;
  &__wrap {
    z-index: 2;
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center; }
  &__logo {
    display: block;
    width: 100%;
    max-width: 25%;
    flex: 0 0 25%;
    .logo-1 {
      display: block; }
    .logo-2 {
      display: none; } }
  &__text {
    display: block;
    width: 100%;
    max-width: 50%;
    flex: 0 0 50%;
    @include font-druk();
    font-size: 28px;
    line-height: 1em;
    color: $primary;
    text-align: center; }
  &__menu {
    position: relative;
    display: block;
    width: 100%;
    max-width: 25%;
    flex: 0 0 25%;
    .menu-toggle {
      margin-left: auto;
      display: block;
      width: 84px;
      height: 32px;
      overflow: hidden; }
    span {
      display: block;
      @include font-fce();
      color: #FFF;
      font-size: 32px;
      line-height: 1em;
      cursor: pointer;
      position: absolute;
      right: 0;
      top: 0;
      transition: all .3s ease-in-out; }
    span.close {
      top: 100%;
      opacity: 0; } }
  &.sticky {
    // background: #000
    background: transparent;
    padding-top: 24px;
    padding-bottom: 24px; }
  &__black {
    .header {
      &__logo {
        .logo-2 {
          display: block; }
        .logo-1 {
          display: none; } }
      &__text {
        color: #000; }
      &__menu {
        span {
          color: #000; } } }
    .menu-toggle {
      span.close {
        color: #FFF; } }
    &.sticky {
      background: transparent; } }

  @include maxtablet() {
    &__logo {
      img {
        height: 18px;
        width: auto; } }
    padding: 16px 20px;
    &__text {
      font-size: 12px;
      line-height: 1em; }
    &__menu {
      .menu-toggle {
        height: 18px; }
      span {
        font-size: 18px;
        line-height: 1em; } } } }

.main-menu {
  z-index: 2;
  position: fixed;
  height: 100vh;
  width: 100vw;
  left: 0;
  top: 0;
  display: none;
  &__bg {
    z-index: 2;
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: url("../images/img-bg-menu.jpg") no-repeat center;
    background-size: cover;
    display: none;
    .bg-hover {
      z-index: 1;
      display: none;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;
      transform: scale(1.2);
      transition: all .4s ease-in-out;
      &.animate {
        transform: scale(1); } }
    span {
      z-index: 2;
      display: none;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      @include font-druk();
      color: $primary;
      font-size: 30vw;
      line-height: 1em;
      padding-top: 100px;
      padding-bottom: 80px;
      text-align: center; } }
  &__fade-top,
  &__fade-bot {
    display: block;
    position: absolute;
    width: 100%;
    height: 50%;
    background: #000;
    left: 0;
    transition: all .4s ease-in-out; }
  &__fade-top {
    top: 0;
    transform: translate(0px, -100%); }
  &__fade-bot {
    bottom: 0;
    transform: translate(0px, 100%); }
  &__wrap {
    position: relative;
    min-height: 100vh; }
  ul {
    z-index: 3;
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    padding: 32px;
    text-align: center;
    display: none;
    &> li {
      display: inline-block;
      vertical-align: middle;
      position: relative;
      &> a {
        display: inline-block;
        vertical-align: middle;
        @include font-druk();
        font-size: 6vw;
        line-height: 1em;
        color: $primary;
        transition: all .3s ease;
        &::after {
          content: "â€¢";
          display: inline-block;
          @include font-druk();
          font-size: 6vw;
          line-height: 1em;
          color: $primary;
          margin: 0 1vw; }
        &:hover {
          color: #FFF; } }
      &:last-child > a::after {
        display: none; }

      &.disabled {
        a {
          pointer-events: none;
          color: #FFF; } } } }
  @include maxtablet() {
    &__bg {
      background: url("../images/img-bg-menu-sm.jpg") no-repeat;
      background-position: center -32px;
      background-size: contain; }
    ul {
      bottom: 64px; }
    ul > li > a {
      font-size: 10vw;
      line-height: 1em;
      &::after {
        font-size: 10vw;
        line-height: 1em; } }
    ul > li:nth-child(3) > a::after {
      display: none; } } }

body.menu-open {
  overflow: hidden;
  .header {
    &__menu {
      .menu-toggle {
        span {
          top: -100%;
          opacity: 0; }
        span.close {
          top: 0;
          opacity: 1; } } } } }





