
.page-ourpeople {
  &__one {
    background: $primary;
    min-height: 100vh;
    color: $primary;
    padding: 10.417vw 5vw 9.375vw;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    .wrap {
      display: flex;
      align-items: center;
      justify-content: center; }
    h1 {
      // font-size: 174px
      font-size: 9.115vw;
      // line-height: 154px
      line-height: .9em;
      @include font-druk();
      font-weight: 400;
      color: #000;
      position: relative;
      margin-bottom: 0;
      img {
        position: absolute;
        width: 30.208vw;
        height: 17.188vw;
        bottom: 4.271vw;
        left: 18.229vw; }
      small {
        position: absolute;
        right: 0;
        bottom: 40px;
        @include font-ahg();
        font-weight: 600;
        font-size: 0.938vw;
        line-height: 1em;
        max-width: 20.313vw; } }
    .arrow {
      position: absolute;
      bottom: 3.646vw;
      left: 0;
      right: 0;
      margin: 0 auto;
      img {
        display: block;
        margin: 0 auto;
        width: 24px;
        height: 40px; } } }

  &__two {
    background: #000;
    .wrap {
      display: flex;
      align-items: center;
      justify-content: center; }
    .tagline-marquee {
      overflow: hidden;
      padding: 50px 0;
      margin-bottom: 30px;
      h2 {
        // font-size: 440px
        font-size: 22.865vw;
        // line-height: 365px
        line-height: 1em;
        @include font-druk();
        font-weight: 600;
        white-space: nowrap;
        color: $primary;
        letter-spacing: 1.2vw; } }
    &-text {
      // max-width: 1325px
      max-width: 70.417vw;
      margin: 0 auto 5.208vw;
      text-align: center;
      p {
        @include font-ahg();
        // font-size: 30px
        font-size: 1.563vw;
        // line-height: 36px
        line-height: 1em;
        font-weight: medium;
        color: #fff; } }
    &-people {
      padding: 0 8.333vw;
      padding-bottom: 8.333vw;
      .people-item {
        display: flex;
        align-items: flex-start;
        margin-bottom: 64px;
        &:nth-last-child(odd) {
          .photo {
            order: 2;
            margin-right: 0;
            margin-left: 95px;
 }            // margin-top: -90px
          .text {
            order: 1;
 }            // margin-top: 90px
          .name {
            text-align: right; } }

        .photo {
          // width: 652px
          width: 33.958vw;
          // height: 885px
          height: 46.198vw;
          // margin-right: 95px
          margin-right: 4.948vw;
          img {
            display: block;
            max-height: 100%;
            max-width: 100%; } }
        .name {
          @include font-druk();
          // font-size: 214px
          font-size: 11.146vw;
          // line-height: 178px
          line-height: .9em;
          max-width: 40.781vw;
          color: $primary;
          margin-bottom: 40px; }
        .position {
          @include font-ahg();
          // font-size: 30px
          font-size: 1.563vw;
          // line-height: 40px
          line-height: 1em;
          font-weight: bold;
          color: #fff;
          margin-bottom: 40px; }
        .quote {
          // max-width: 720px
          max-width: 37.500vw;
          @include font-ahg();
          // font-size: 34px
          font-size: 1.771vw;
          // line-height: 44px
          line-height: 1em;
          color: #FFF; } } } }

  &__three {
    background: $primary;
    .wrap {
      padding: 5.208vw 7.813vw; }
    &-title {
      font-size: 30px;
      line-height: 40px;
      @include font-ahg();
      text-align: center;
      color: #000;
      // margin-bottom: 100px
      margin-bottom: 5.208vw; }
    &-logos {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      justify-content: space-between;
      // margin-left: -35px
      margin-left: -1.823vw;
      // margin-right: -35px
      margin-right: -1.823vw;
      .logo {
        // padding-left: 35px
        padding-left: 1.823vw;
        // padding-right: 35px
        padding-right: 1.823vw;
        img {
          // height: 140px
          height: 7.292vw;
          // max-width: 310px
          max-width: 16.146vw;
          object-fit: contain; } }
      & + .page-ourpeople__three-logos {
        // margin-top: 160px
        margin-top: 8.333vw; }
      &:nth-child(3) {
        .logo {
          img {
            height: 5.292vw; } } } } }
  &__four {
    background-image: url(../images/bg-sfour-ourpeople.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    padding: 3.125vw;
    &-top {
      display: flex;
      align-items: center;
      margin-bottom: 72px;
      justify-content: space-between;
      .left {
        display: flex;
        align-items: center;
        margin-left: -32px;
        margin-right: -32px;
        @include font-druk();
        // font-size: 39px
        font-size: 2.083vw;
        line-height: 1em;
        font-weight: 500;
        color: #000;
        .item {
          padding-right: 32px;
          padding-left: 32px; }
        a {
          color: #000;
          text-decoration: none; } }
      .right {
        @include font-druk();
        font-size: 2.083vw;
        line-height: 1em;
        font-weight: 500;
        color: $primary; } } }
  @include ltablet() {
    &__one {
      h1 {
        font-size: 7.292vw;
        line-height: 1em;
        small {
          bottom: -40px; } } } }

  @include maxtablet() {
    &__one {
      h1 {
        font-size: 18vw;
        line-height: .8em;
        br {
          display: none; }
        img {
          width: 53.426vw;
          height: 30.370vw;
          bottom: 48.271vw;
          left: 33.229vw; }
        small {
          font-size: 3vw;
          bottom: 0;
          max-width: 45vw; } } }
    &__two {
      .tagline-marquee {
        margin-bottom: 0; }
      &-text {
        margin-bottom: 60px;
        p {
          font-size: 16px; } }
      &-people {
        .people-item {
          // align-items: flex-end
          flex-direction: column;
          .photo {
            width: 100%;
            max-width: 100%;
            height: auto;
            margin-bottom: 24px; }
          .quote {
            font-size: 12px;
            line-height: 1.2em;
            max-width: 100%; }
          .name {
            margin-bottom: 10px;
            max-width: 100%;
            text-align: left; }
          .position {
            font-size: 12px;
            line-height: 1.2em;
            margin-bottom: 10px;
            max-width: 100%; }
          &:nth-last-child(odd) {
            .photo {
              margin-top: 0;
              margin-left: 0;
              order: 1; }
            .name {
              text-align: left; } } } } }
    &__three {
      &-title {
        font-size: 16px;
        line-height: 1em; } }
    &__four {
      &-top {
        align-items: flex-start;
        .left {
          margin-left: -10px;
          margin-right: -10px;
          font-size: 16px;
          line-height: 1em;
          flex-wrap: 0 0 57%;
          max-width: 57%;
          flex-wrap: wrap;
          .item {
            padding-right: 10px;
            padding-left: 10px;
            &.email {
              margin-bottom: 16px; } } }
        .right {
          font-size: 16px;
          line-height: 1em; } } } } }
