/* Fonts */

@include fontGenerator("Alte Haas Grotesk",(400),(alte-haas-grotesk));
@include fontGenerator("Alte Haas Grotesk",(600),(alte-haas-grotesk-bold));

@include fontGenerator("Futura Condensed Extrabold",(700),(futura-condensed-extrabold));

@include fontGenerator("Ancient Medium",(500),(ancient-medium));

@include fontGenerator("Druk",(500),(druk-medium));

@mixin font-ahg {
  font-family: "Alte Haas Grotesk", -apple-system, BlinkMacSystemFont,"Segoe UI","Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol"; }

@mixin font-fce {
  font-family: "Futura Condensed Extrabold", -apple-system, BlinkMacSystemFont,"Segoe UI","Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
  font-weight: 700; }

@mixin font-ancient {
  font-family: "Ancient Medium", -apple-system, BlinkMacSystemFont,"Segoe UI","Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
  font-weight: 500; }

@mixin font-druk {
  font-family: "Druk", -apple-system, BlinkMacSystemFont,"Segoe UI","Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol"; }

