/* GENERAL */
* {
  cursor: none !important; }

.cursor {
  display: block;
  width: 14px;
  height: 14px;
  border-radius: 7px;
  // background: #c93513
  background: #fff;
  position: fixed;
  z-index: 201;
  pointer-events: none;
  transform: translate(-5px, -5px);
  transition: width .3s ease, height .3s ease;
  &.hasimg {
    width: 30.208vw;
    height: 17.188vw;
    border-radius: 0;
    background: url("../images/our-people-s1.jpg");
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    transform: translate(0, 0); }
  @include maxtablet() {
    display: none; } }

.trail {
  position: absolute;
  pointer-events: none;
  z-index: 200;
  height: 4px;
  width: 4px;
  border-radius: 2px;
  // background: #c93513
  background: #fff;
  &.hasimg {
    display: none; }
  @include maxtablet() {
    display: none; } }

html, body {
  display: block;
  max-width: 100vw;
  overflow-x: hidden; }

.sm-only {
  display: none;
  @include maxtablet() {
    display: block; } }
