.page-home {
  .rtf {
    color: $primary;
    @include font-druk();
    font-size: 76px;
    line-height: 1em;
    text-decoration: underline;
    opacity: 1;
    transition: all .3s ease;
    &:hover {
      opacity: 0.7; } }
  @include maxtablet() {
    .rtf {
      font-size: 32px;
      line-height: 1em; } } }

.page-home__one {
  background: $primary;
  min-height: 100vh;
  color: $primary;
  padding: 10.417vw 5vw 9.375vw;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  .wrap {
    display: flex;
    align-items: center;
    justify-content: center; }
  h1 {
    font-size: 9.115vw;
    line-height: .9em;
    @include font-druk();
    font-weight: 400;
    color: #000;
    position: relative;
    margin-bottom: 0;
    small {
      position: absolute;
      right: 0;
      bottom: 40px;
      @include font-ahg();
      font-weight: 600;
      font-size: 0.938vw;
      line-height: 1em;
      max-width: 20.313vw; } }
  .arrow {
    position: absolute;
    bottom: 3.646vw;
    left: 0;
    right: 0;
    margin: 0 auto;
    img {
      display: block;
      margin: 0 auto;
      width: 24px;
      height: 40px; } }

  @include maxtablet {
    h1 {
      font-size: 15vw;
      line-height: .8em;
      br {
        display: none; }
      img {
        width: 53.426vw;
        height: 30.370vw;
        bottom: 48.271vw;
        left: 33.229vw; }
      small {
        font-size: 2vw;
        bottom: 0;
        max-width: 30vw; } } } }

.page-home__two {
  min-height: 100vh;
  background: #000;
  padding-top: 120px;
  padding-bottom: 100px;
  position: relative;
  overflow: hidden;
  .wrap {
    padding-left: 60px;
    padding-right: 60px; }
  .huge-text {
    display: block;
    text-align: center;
    @include font-druk();
    font-size: 58vw;
    line-height: 0.8em;
    color: #FFFFFF; }
  .small-text {
    position: absolute;
    display: block;
    max-width: 370px;
    color: $primary;
    bottom: 20%;
    left: 100px;
    transform: rotate(-90deg);
    transform-origin: left; }

  @include maxtablet() {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-bottom: 60px;
    .wrap {
      width: 100%;
      padding-left: 30px;
      padding-right: 30px; }
    .huge-text {
      font-size: 90vw;
      word-break: break-all; }
    .small-text {
      font-size: 3vw;
      line-height: 1em;
      left: 14vw;
      bottom: 17%; }
    .wrap-buttons {
      text-align: center;
      margin-top: 40px; }
    .rtf {
      @include font-druk();
      font-size: 32px;
      line-height: 1em;
      color: $primary;
      text-decoration: underline; } } }

.page-home__three {
  min-height: 100vh;
  background-color: #000;
  background-image: url("../images/img-bg-home-02.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding-top: 120px;
  padding-bottom: 80px;
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: flex-end;
  .wrap {
    padding-left: 60px;
    padding-right: 60px;
    text-align: center;
    width: 100%; }
  h2 {
    @include font-druk();
    font-size: 8.5vw;
    font-weight: 500;
    line-height: 1em;
    color: $primary;
    display: flex;
    align-items: center;
    justify-content: center;
    span {
      display: inline-block;
      vertical-align: middle;
      order: 2; }
    span.oa {
      font-size: 2vw;
      line-height: 1em;
      background: $primary;
      color: #000;
      padding: 0 10px;
      margin-right: 2vw;
      order: 1; }
    span.date {
      font-size: 2vw;
      line-height: 1em;
      margin-left: 2vw;
      order: 3; } }

  .desc {
    margin: 0 0 40px;
    p {
      color: #FFF;
      font-size: 30px;
      line-height: 1.2em; } }
  .rtf {
    color: $primary; }

  @include maxtablet() {
    background-image: url("../images/img-bg-home-02-sm.jpg");
    align-items: center;
    .wrap {
      padding-left: 30px;
      padding-right: 30px; }
    h2 {
      font-size: 22vw;
      line-height: 1em;
      display: block;
      span {
        display: block; }
      span.oa,
      span.date {
        display: inline-block;
        font-size: 32px;
        line-height: 1em;
        margin: 0 10px; } }
    .desc p {
      font-size: 16px;
      line-height: 1.3em; } } }

.page-home__four {
  min-height: 100vh;
  background: $primary;
  padding-top: 120px;
  padding-bottom: 80px;
  position: relative;
  .wrap {
    padding-left: 60px;
    padding-right: 60px;
    text-align: center;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    .col-left {
      width: 100%;
      max-width: 15%;
      flex: 0 0 15%;
      order: 1; }
    .col-main {
      width: 100%;
      max-width: 70%;
      flex: 0 0 70%;
      order: 2; }
    .col-right {
      width: 100%;
      max-width: 15%;
      flex: 0 0 15%;
      order: 3; } }
  span.oa {
    display: inline-block;
    @include font-druk();
    font-size: 2vw;
    line-height: 1em;
    background: #000;
    color: #FFF;
    padding: 0 10px; }
  span.date {
    display: inline-block;
    color: #000;
    @include font-druk();
    font-size: 2vw;
    line-height: 1em; }
  h2 {
    @include font-druk();
    font-size: 8.5vw;
    font-weight: 500;
    line-height: 1em;
    color: #000; }
  .img {
    margin: 0 0 40px;
    img {
      display: block;
      margin: 0 auto;
      max-width: 25vw;
      height: auto;
      width: auto; } }
  .desc {
    margin: 0 0 40px;
    p {
      color: #000;
      font-size: 30px;
      line-height: 1.2em; } }
  .rtf {
    color: #000; }
  @include maxtablet() {
    .wrap {
      padding-left: 30px;
      padding-right: 30px;
      flex-wrap: wrap;
      .col-left,
      .col-right {
        max-width: 50%;
        flex: 0 0 50%;
        order: 1; }
      .col-main {
        max-width: 100%;
        flex: 0 0 100%;
        order: 2; } }
    span.oa,
    span.date {
      display: inline-block;
      font-size: 32px;
      line-height: 1em;
      margin: 0 10px 20px; }
    h2 {
      font-size: 22vw;
      line-height: 1em; }
    .img {
      img {
        max-width: 60vw; } }
    .desc p {
      font-size: 16px;
      line-height: 1.3em; } } }

.page-home__five {
  min-height: 100vh;
  background-color: #000;
  background-image: url("../images/img-bg-home-05.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding-top: 120px;
  padding-bottom: 80px;
  position: relative;
  .wrap {
    min-height: calc(100vh - 200px);
    padding-left: 60px;
    padding-right: 60px;
    width: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    .col-left {
      width: 100%;
      max-width: 60%;
      flex: 0 0 60%; }
    .col-right {
      width: 100%;
      max-width: 40%;
      flex: 0 0 40%;
      text-align: right; } }
  span.oa {
    display: inline-block;
    @include font-druk();
    font-size: 2vw;
    line-height: 1em;
    background: $primary;
    color: #000;
    padding: 0 10px;
    margin-bottom: 15px; }
  h2 {
    @include font-druk();
    font-size: 8.5vw;
    font-weight: 500;
    line-height: 1em;
    color: $primary; }
  .desc {
    margin: 0 0 40px;
    p {
      color: #FFF;
      font-size: 16px;
      line-height: 1.2em; } }

  @include maxtablet() {
    .wrap {
      padding-left: 30px;
      padding-right: 30px;
      flex-wrap: wrap;
      .col-left,
      .col-right {
        max-width: 100%;
        flex: 0 0 100%;
        text-align: center; } }
    span.oa {
      display: inline-block;
      font-size: 32px;
      line-height: 1em;
      margin: 0 10px 20px; }
    h2 {
      font-size: 22vw;
      line-height: 1em;
      br {
        display: none; } }
    .desc:not(.sm-only) {
      display: none; } } }

.page-home__six {
  min-height: 100vh;
  background-color: #000;
  padding-top: 120px;
  padding-bottom: 80px;
  position: relative;
  display: flex;
  align-items: center;
  .wrap {
    position: relative;
    width: 100%;
    padding-left: 60px;
    padding-right: 60px;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;
    .col-main {
      position: relative;
      width: 100%;
      max-width: 100%;
      flex: 0 0 100%;
      margin-bottom: 60px;
      .img {
        display: block;
        width: 100%;
        position: absolute;
        top: calc(50% - 12vw);
        left: 0;
        img {
          display: block;
          width: 24vw;
          height: 24vw;
          margin: 0 auto; } } }
    .col-bot-l {
      width: 100%;
      max-width: 30%;
      flex: 0 0 30%; }
    .col-bot-c {
      width: 100%;
      max-width: 40%;
      flex: 0 0 40%;
      text-align: center; }
    .col-bot-r {
      width: 100%;
      max-width: 30%;
      flex: 0 0 30%;
      text-align: right; } }
  span.oa {
    display: inline-block;
    @include font-druk();
    font-size: 2vw;
    line-height: 1em;
    background: $primary;
    color: #000;
    padding: 0 10px;
    margin-bottom: 60px; }
  h2 {
    @include font-druk();
    font-size: 24vw;
    line-height: 1em;
    color: $primary;
    font-weight: 500;
    span.star {
      display: inline-block;
      position: relative;
      margin: 0 2vw;
      top: 6vw; } }
  .desc {
    p {
      font-size: 16px;
      line-height: 1.2em;
      color: #FFF; } }
  .date {
    @include font-druk();
    color: #FFF;
    font-size: 2vw;
    line-height: 1em;
    margin-bottom: 60px;
    display: block;
    text-align: right; }
  .meta {
    display: block;
    font-size: 2vw;
    line-height: 1em;
    color: #FFF;
    @include font-druk(); }
  @include maxtablet() {
    padding-top: 64px;
    padding-bottom: 64px;
    .wrap {
      padding-left: 30px;
      padding-right: 30px;
      .col-main {
        order: 1;
        .img {
          position: relative;
          img {
            width: 60vw;
            height: 60vw; } } }
      .col-bot-l {
        order: 3;
        max-width: 100%;
        flex: 0 0 100%;
        text-align: center;
        margin-bottom: 20px;
        span.oa {
          display: none; } }
      .col-bot-c {
        order: 4;
        max-width: 100%;
        flex: 0 0 100%;
        text-align: center;
        padding-top: 20px; }
      .col-bot-r {
        order: 2;
        max-width: 100%;
        flex: 0 0 100%;
        text-align: center;
        margin-bottom: 20px;
        .date {
          display: none; } } }
    .sm-only {
      text-align: center;
      span.oa,
      span.date {
        display: inline-block;
        font-size: 32px;
        line-height: 1em;
        margin: 0 10px 20px; } }
    h2 {
      font-size: 40vw;
      line-height: 1em; }
    .meta {
      font-size: 24px;
      line-height: 1.2em; } } }

.page-home__footer {
  background-image: url(../images/bg-sfour-ourpeople.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding: 3.125vw;
  &-top {
    display: flex;
    align-items: center;
    margin-bottom: 72px;
    justify-content: space-between;
    .left {
      display: flex;
      align-items: center;
      margin-left: -32px;
      margin-right: -32px;
      @include font-druk();
      // font-size: 39px
      font-size: 2.083vw;
      line-height: 1em;
      font-weight: 500;
      color: #000;
      .item {
        padding-right: 32px;
        padding-left: 32px; }
      a {
        color: #000;
        text-decoration: none; } }
    .right {
      @include font-druk();
      font-size: 2.083vw;
      line-height: 1em;
      font-weight: 500;
      color: $primary; } } }


