.page-landing {
  background: url("../images/img-landing-bg.jpg") no-repeat center;
  background-size: cover;
  min-height: 100vh;
  color: $primary;
  padding: 175px 5vw 7vh;
  position: relative;
  &__title {
    color: $primary;
    font-size: 72px;
    line-height: 1em;
    text-align: center;
    margin: 0;
    span {
      color: #FFF;
      i {
        color: $primary; } } }
  &__date {
    display: block;
    text-align: center;
    color: #FFF;
    @include font-ancient();
    font-size: 92px;
    line-height: 1.1em;
    margin-bottom: 10px;
    .ic {
      display: inline-block;
      margin: 0 10px; }
    span {
      display: inline-block;
      margin: 0 60px; } }
  &__subtitle {
    display: block;
    text-align: center;
    @include font-fce();
    font-size: 26px;
    line-height: 1em; }
  &__desc {
    padding-top: 30px;
    text-align: center;
    p {
      font-size: 21px;
      line-height: 1.2em;
      font-weight: 600;
      span.text-white,
      a.text-white,
      .text-white {
        color: #FFF; }
      a.text-white:hover {
        text-decoration: underline; } } }
  &__info {
    padding-top: 30px;
    text-align: center;
    font-size: 21px;
    line-height: 1.2em;
    font-weight: 600;
    span.title {
      display: block; }
    a.text-white {
      color: #FFF;
      &:hover {
        text-decoration: underline; } } }
  .bg-video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    video {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover; } }
  .wrap {
    max-width: 1450px;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    z-index: 2; }

  @include maxtablet() {
    &:before {
      content: "";
      width: 100%;
      height: 100%;
      filter: blur(1px);
      position: absolute;
      top: 0;
      left: 0;
      z-index: 0;
      background: #000;
      opacity: .5; }
    &__title {
      font-size: 40px;
      line-height: 1em; }
    &__date {
      font-size: 54px;
      line-height: 1em;
      .ic {
        height: 30px;
        width: auto; }
      span {
        display: block;
        margin: 0 20px; } }
    &__subtitle {
      font-size: 20px;
      line-height: 1em; }
    &__desc {
      p {
        font-size: 16px;
        line-height: 1.2em; } }
    &__info {
      font-size: 16px;
      line-height: 1.2em; }
    .bg-video {
      display: none; } } }



