    line-height: 1em;
    @include font-druk();
    font-weight: 500;
    color: $primary;
    text-align: center;
    margin-bottom: 40px; }
  .desc {
    max-width: 70%;
    margin-left: auto;
    margin-right: auto;
    p {
      font-size: 29px;
      line-height: 1.5em;
      color: #FFF;
      text-align: center; } }
  .arrow {
    position: absolute;
    bottom: 3.646vw;
    left: 0;
    right: 0;
    margin: 0 auto;
    img {
      display: block;
      margin: 0 auto;
      width: 24px;
      height: 40px; } }
  .wrap {
    position: relative;
    width: 100%; }
  @include maxtablet() {
    min-height: 80vh;
    align-items: flex-end;
    background-image: url("../images/img-bg-inspiration-single-xs.jpg");
    background-position: center;
    h1 {
      font-size: 18vw;
      line-height: 1em; }
    .desc {
      display: none; } } }

.page-inspiration-s__two {
  p {
    font-size: 29px;
    line-height: 1.5em;
    font-weight: 600; }
  .wrap {
    position: relative;
    width: 100%;
    padding: 40px; }
  @include maxtablet() {
    p {
      font-size: 16px;
      line-height: 1.3em; }
    .wrap {
      padding: 20px; } } }

.page-inspiration-s__three {
  .wrap {
    position: relative;
    width: 100%; }
  .video-wrap {
    display: block;
    padding-top: 56.2%;
    position: relative;
    .btn-play {
      content: "";
      z-index: 2;
      display: block;
      width: 20vw;
      height: 20vw;
      background: url("../images/ic-play.svg") no-repeat center;
      background-size: contain;
      position: absolute;
      top: calc(50% - 10vw);
      left: calc(50% - 10vw);
      pointer-events: none; }
    iframe,
    video {
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      border: 0; } } }


